import './App.css';
import './bootstrap.css';
import React,{Component} from 'react';
import {BrowserRouter as Router, Link} from 'react-router-dom';
import './Style.css';
class Keyword extends React.Component{
    render(){
        return(
            <div className='key-contain'>
            <h5 className='key-header'>Add keywords to Discover Resources</h5>
            <form className='keyword-form mb-4'>
  <div className="form-group">
    <input type="text" className="form-control" placeholder="Add Keywords..."/>
  </div>
</form>
<p className='pop-key mb-2'>POPULAR KEYWORDS</p>
<div className="card-groups">
  <div className="card">
    <div className="card-body">
      <p className="card-text"><small className="text-muted">Metal Organic Framework</small></p>
    </div>
  </div>
  <div className="card">
    <div className="card-body">
      <p className="card-text"><small className="text-muted">Machine learning</small></p>
    </div>
  </div>
  <div className="card">
    <div className="card-body">
      <p className="card-text"><small className="text-muted">Adsorption</small></p>
    </div>
  </div>
  <div className="card">
    <div className="card-body">
      <p className="card-text"><small className="text-muted">PDMS</small></p>
    </div>
  </div>
</div>
</div>
        )
    }
}
export default Keyword