import './App.css';
import './bootstrap.css';
import React,{Component} from 'react';
import Navbar from './Navbar';
import {BrowserRouter as Router, Link,Switch,Route} from 'react-router-dom';
import Feed from './Feed';

class Home extends Component{

render(){
  return(
    <div className='container'>
        <div className="navbar navbar-expand-lg navbar-light bg-white pl-0 py-3">
  <a className="navbar-brand" href="#">Scholarlys</a>
  </div>
  <div className='row'>
      <div className='col-12 col-lg-2 nav-container'>{<Navbar/>}</div>
      <div className='col-12 col-lg-8 feed-container px-3'>{this.props.feed}</div>
      <div className='col-12 col-lg-2 filter-container'>
        {this.props.filter}
      </div>
  </div>
    </div>
  )
}
}
export default Home;
