import './App.css';
import './bootstrap.css';
import React,{Component} from 'react';
import {BrowserRouter as Router, Link} from 'react-router-dom';
import './Style.css';

class Feed extends Component{
constructor(props){
    super(props);
    this.state={result:[],show:''}
}
async componentDidMount(){
var res = await fetch('/assets/feed-data.json')
var res = await res.json()
this.setState({result:res})
console.log(this.state.result);

}
click(key){
    var target ="comment-container-"+key;
    var elem = document.getElementById(target);
    elem.classList.remove("hide");
    this.setState({show:'block',target:target})
}
close(){
    document.getElementById(this.state.target).classList.add('hide');
    this.setState({show:'',target:''})

}
render(){
    const {result} = this.state
  return(
    <>
    <h5 className='heading'>{this.props.head}</h5>
    {result.map(r=>(
        <div className='feed mb-3' onClick={()=>{this.click(r.key)}} key={r.key}>
            <div className='image-container col-lg-3 col-md-3 col-4'>
                <img className='thumb-image' src={r.image} alt=''/>
            </div>
            <div className='text-container'>
            <b className="main-header"><span>{r.header}</span></b>
            <div className='meta-container'>
                <span className='keyword'>{r.Keyword}</span>
                <div className='date-container'>
                    <i className='mr-2'>{r.date}</i>
                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24" fill="none" stroke="#66F" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" icon="" style={{position: 'relative', top: '0px', left: '0px', width: '1.4em', height: '1.4em'}}><path d="M19 21l-7-5-7 5V5a2 2 0 0 1 2-2h10a2 2 0 0 1 2 2z"></path></svg>
                </div>
            </div>
            </div>
            <div className={"comment-container hide"} id={"comment-container-"+r.key}>
    <h5 className="d-inline-block mb-3">{r.header}</h5>
    <img src={r.image} alt='' className='main-image'/>
    <p className='mt-4'>{r.abstract}</p>
    
  </div>
        </div>
       
    ))}
    <div className="drop-shadow" style={{display: this.state.show}} onClick={()=>{this.close()}}></div>
    </>
  )
}
}
export default Feed;
