import './App.css';
import './bootstrap.css';
import React,{Component} from 'react';
import {BrowserRouter as Router, Link} from 'react-router-dom';

class Navbar extends Component{

render(){
  return(
    <div className='navigation bg-light'>
        <div className='profile pt-2'>
            <img className='profile-image mx-auto my-2' src='/assets/inline.jpg' alt=""/>
        </div>
        <div className='nav-links mx-auto'>
        <p className='user-name'>Deep Dasgupta</p>
        <Link className='links' to='/'><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24" fill="none" stroke="#66F" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" icon="" style={{position: 'relative', top: '0px', left: '0px', width: '1.4em', height: '1.4em'}}><polygon points="12 2 2 7 12 12 22 7 12 2"></polygon><polyline points="2 17 12 22 22 17"></polyline><polyline points="2 12 12 17 22 12"></polyline></svg><span className='nav-opt'>Feed</span></Link>
        <Link className='links' to='/keywords'><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24" fill="none" stroke="#66F" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" icon="" style={{position: 'relative', top: '0px', left: '0px', width: '1.4em', height: '1.4em'}}><line x1="3" y1="12" x2="21" y2="12"></line><line x1="3" y1="6" x2="21" y2="6"></line><line x1="3" y1="18" x2="21" y2="18"></line></svg><span className='nav-opt'>Keywords</span></Link>
        <Link className='links' to='/bookmark'><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24" fill="none" stroke="#66F" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" icon="" style={{position: 'relative', top: '0px', left: '0px', width: '1.4em', height: '1.4em'}}><path d="M19 21l-7-5-7 5V5a2 2 0 0 1 2-2h10a2 2 0 0 1 2 2z"></path></svg><span className='nav-opt'>Bookmark</span></Link>
        <Link className='links' to='/logout'><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24" fill="none" stroke="#66F" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" icon="" style={{position: 'relative', top: '0px', left: '0px', width: '1.4em', height: '1.4em'}}><path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"></path><polyline points="16 17 21 12 16 7"></polyline><line x1="21" y1="12" x2="9" y2="12"></line></svg><span className='nav-opt'>Logout</span></Link>

        </div>
    </div>
  )
}
}
export default Navbar;
